//Major Minor Page Styles

.post-26990, .post-26992 {
    padding-bottom: 0;
    .article__content{
        min-height: 0!important;   
    }
}
.majors-minors-section {
    padding-top: 0;
    
    img {
        transition-duration: .3s;
    }
}


.major-minor {
    height: 130px;
    margin-bottom: .5rem;
    overflow: hidden;
    transition-duration: .3s;
    position: relative;
    background-color: #fff;
    &:not(.unit-color) {
        border: 1px solid #eee;
        border-left: none;
        @extend .shadow;
    }
    &.unit-color {
        color: white;
        @extend .shadow;
        &:after {
            color: white!important;
            border-color: white!important;
        }
        &.open:after {
            color: white;
        }
        &:before {
            content: "";
            position: absolute;
            display: block;
            top: 0;
            height: 100%;
            width: 100%;
            margin-left: 130px;
            background-color: rgba(0,0,0,.1);
        }
        &:hover:before {
            content: none;
        }
    }
    &.accordion-title {
        padding: 0;
        background-color: white;
        z-index: 1;

         &:after {
            content: "\e028";
             font-size: 20px;
            -webkit-transform: rotate(-90deg);
            -ms-transform: rotate(-90deg);
            transform: rotate(-90deg);
            width: 16px;
            height: 16px;
            padding: 2px 2px 1px 1px;
            background: transparent;
            color: #666;
            text-align: center;
            right: .75rem;
            top: 7%;
        }
        &.open:after {
            content: "\e028";
            transform: translateY(-50%) rotate(90deg);
        }
        &.open:before {
            @include min-width ( 768 ) {
                margin-left: 200px;
            }
            margin-left: 130px;
        }
        .last {
            display: none;
        }
        &:hover {
            background-color: #fff;
        }
    }

    @include max-width ( 380 ) {
        .major-info {
            margin-left: 0!important;
        }
    }


    &.open {
        width: 100%;
        margin-bottom: 0rem;
        .major-title {
            padding-right: 3rem;
        }
        &:after {
            letter-spacing: -1px;
            padding-top: 3px;
            height: 16px;
            width: 16px;
            font-size: 20px;
            margin-top: -4px;
            right: .5rem;
            top: 50%;
            border: none;
            color: #666;
        }

        @include min-width ( 768 ) {
            h3 {
                font-size: 2rem;
                padding-top: .5rem;
                margin-bottom: .5rem;
                padding-bottom: .75rem;
                line-height: 2rem;
            }
            .major-title {
                font-size: 1.2rem;
                transition: .3s;
                display: table;
                padding-right: 5rem;
                padding-left: calc(200px + 1rem)
            }
            .major-info {
                font-size: 1.2rem;
            }
            img {
                width: 200px;
                height: 200px;
            }

            .last {
                display: inline;
            }
            .left {
                display: block;
                position: absolute;
                left: 40%;
                top: 1rem;
                @include max-width ( $iPadLandscape ) {
                    top: 0;
                    bottom: 0!important;
                    @include rem ('padding', .5, 0, .5, 1);
                    left: 30%;

                    li {
                        font-size: 80%!important;
                    }
                }
            }
            height: 200px;
            &:after {
                height: 32px;
                width: 33px;
                font-size: 30px;
                right: 1.5rem;
            }

        }
    }

    @media print {
        page-break-inside: avoid;
        display: inline-block;
        width: 49%;
        @include rem('margin-right', .3)
    }

    @include min-width ( $iPadLandscape ) {
        display: inline-block;
        width: 49%;
        @include rem('margin-right', .3)
    }

}

[aria-hidden=false] {
        transition: width .3s;
        width: 100%!important;
    }

.major-title {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    padding-left: calc(130px + 1rem);
    padding-right: 1rem;
    @include min-width ( $iPadPortrait ) {
        transition-duration: .3s;
    }
}

.major-info {

    h3 {
        font-size: 1.3rem;
        line-height: 1.3rem;
        text-transform: none;
        padding-bottom: .5rem;
        margin-bottom: .25rem;
        border-bottom: 1px solid $border;

    }

    .type {
        font-size: 120%!important;
        display: block;
        font-family: 'skolar';
        font-weight: 400;
        margin-top: .25rem;
    }

    @include max-width ( $iPhoneLandscape ) {
        @include rem('padding-right', .6);
        h3 {
            font-size: 1rem;
        }

        .type {
            font-size: 80%!important;
        }
    }

}

[aria-expanded="false"] {
    .major-title h3 {
        @include max-width ( $iPadPortrait ) {
            transition-duration: .3s;
            font-size: 1.2rem!important;
            padding-bottom: .25rem;
            margin-bottom: .15rem;
        }
        @include max-width ( $iPhoneLandscape ) {
            font-size: 1rem!important;
        }
    }
    .type {
        @include max-width ( $iPadPortrait ) {
            transition-duration: .3s;
            font-size: 1rem!important;
        }
        @include max-width ( $iPhoneLandscape ) {
            font-size: .8rem!important;
        }
    }
}

.major-content {
    background-color: #f7f7f7;
    position: relative;
    z-index: 0;
    margin-top: -7rem;
    padding-top: 7rem;
    transition: .3s height;
    width: 100%;

    a {
        color: $text;
        text-decoration: none;
    }

    @include min-width ( $iPadPortrait ) {
        margin-top: -10rem;
        padding-top: 10rem;
        &:before {
            content: "";
            width: 200px;
            top: 0;
            height: calc(100%);
            background: #eee;
            z-index: -1;
            position: absolute;
        }
    }
    @include min-width ( $iPadLandscape ) {
        width: 368px;
        transition: .3s width;
    }
}

.major-info {
    padding: 0 0 .5rem 0;
    margin: 0;
    .row {
        width: 100%;
        padding: 0;
        display: table;
    }
    .prompt {
        text-transform: uppercase;
        font-weight: 700;
        padding: .5rem 1rem;
        display: inline-block;
        font-size: 90%!important;
        color: $text-md;
        width: 100%;
        margin: 0;
        background: #eee;

        @include min-width ( $iPadPortrait ) {
            width: 200px;
            text-align: right;
            background: none;
            padding: .5rem 1rem;
        }
    }
    .answer-content {
        list-style: none;
        padding: 0rem 1rem 0 1rem;
        margin: .5rem 0;
        p {
            margin: 0 0 .75rem 0;
        }
        li, a {
            &:first-of-type {
                margin-top: 0;
            }
            margin-top: .5rem;
            i {
                margin-right: .5rem;
            }
            .icon-faculty-grid-alt-2 {
                margin-right: 5px!important;
            }
            .dashicons {
                font-size: 16px;
                height: 16px;
                width: 16px;
                margin-top: 2px;
                margin-left: -1px;
            }
        }
        @include min-width ( $iPadPortrait ) {
            width: calc(100% - 200px);
            float: right;
            padding: .5rem 1rem .5rem 1rem;
            margin: 0;
        }
    }
}